@keyframes kenburns-top-right {
  0% {
    transform: scale(1) translate(0, 0);
    transform-origin: 84% 16%;
  }
  100% {
    transform: scale(1.25) translate(20px, -15px);
    transform-origin: right top;
  }
}

@keyframes text-focus-in {
  0% {
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    filter: blur(0);
    opacity: 1;
  }
}
.Sobre {
  width: 100%;
  color: #72727e;
  background: #0e091b;
}
.Sobre svg {
  width: 100px;
  transform: rotate(180deg);
  transition: 3s;
}
.svgMode {
  fill: #ffffe0;
  filter: drop-shadow(0 0 2rem #ffffe0);
}
.geral {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.geral p {
  max-width: 400px;
  font-weight: bold;
  font-size: 1.3rem;
}

.sectionImg {
  width: 500px;
  height: 450px;
}
.sectionImg img {
  border-radius: 50%;
  width: 15rem;
  border: double 10px #ffffe0;
}
.anima {
  opacity: 0.7;
  width: 300px;
  border-radius: 50%;
  animation: kenburns-top-right 5s ease-out both;
}
.animap {
  animation: text-focus-in 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
}
